import { Injectable } from '@angular/core';
import { Router } from '@angular/router';
import { ConfirmationDialogService } from '../components/confirmation-dialog/confirmation-dialog.service';
import { DataService, LabelService } from '../services';

@Injectable({
    providedIn: 'root'
})
export class IsReflabActive  {

    private isReflabActive = false;
    
    constructor(private router: Router,
        public dataService: DataService,
        public confirmationDialogService: ConfirmationDialogService,
        public labelService: LabelService) { }
    
        canActivate() {
        if (this.dataService.appData.authDetails['IsReflabActive']) {
            this.isReflabActive = this.dataService.appData.authDetails['IsReflabActive'];
        }
        if (this.isReflabActive) {
            return true;
        }
        return false;
    }

}