import { Injectable } from '@angular/core';
import { Router } from '@angular/router';
import { CommonService, DataService, LabelService } from '../services';
import { EnmLanguage, EnmCountry } from '../shared/constants/enum';

@Injectable({
    providedIn: 'root'
})
export class MaintenanceMode  {

    public isMaintenanceMode: boolean;
    
    constructor(private router: Router, private commonService: CommonService, public dataService: DataService, public labelService: LabelService) {
        this.commonService.getUserDDLDetails(EnmLanguage.English, EnmCountry.UnitedStates, null).subscribe({next:() => {
            this.dataService.appData.isMaintenanceMode = false;
            this.dataService.data.next(this.dataService.appData);
        }, error:(err) => {
            console.log(JSON.stringify(err));
            this.commonService.showCommonToastrError();
        }});
    }
    
    canActivate() {
        this.isMaintenanceMode = this.dataService.appData.isMaintenanceMode;

        if (!this.isMaintenanceMode) {
            return true;
        }
        this.router.navigate(['/maintenance']);
    }

}
