import { Component, OnInit, ViewChild } from '@angular/core';
import { CommonService } from '../../../services';
import { SideFilter } from '../../../models/side-filter';
import { HeaderService } from '../../../services/header.service';
import { animate, state, style, transition, trigger } from '@angular/animations';
import { PatientService } from '../../../services/patient.service';
import { DashboardScreen } from '../../../models/label';
import { LabelService, DataService } from '../../../services';
import { SidefilterService } from '../../../services/sidefilter.service';
import { EnmModule } from '../../../shared/constants/enum';
import { SidemenuService } from '../../../services/sidemenu.service';
import { UntypedFormGroup, UntypedFormBuilder } from '@angular/forms';
import { UserService } from '../../../services/user.service';
import { DosScreen } from '../../../models/label';
import { AlertMessageComponent } from '../../../components/alert-message/alert-message.component';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { MatLegacyPaginator as MatPaginator } from '@angular/material/legacy-paginator';
import { MatSort } from '@angular/material/sort';
import { MatLegacyTableDataSource as MatTableDataSource } from '@angular/material/legacy-table';
// declare const gtag: Function;
declare const gtag: (...arg) => void;


@Component({
    selector: 'app-dos',
    templateUrl: './dos.component.html',
    styleUrls: ['./dos.component.css'],
    animations: [
        trigger('detailExpand', [
            state('collapsed', style({ height: '0px', minHeight: '0' })),
            state('expanded', style({ height: '*' })),
            transition('expanded <=> collapsed', animate('225ms cubic-bezier(0.4, 0.0, 0.2, 1)')),
        ]),
    ],
})
export class DosComponent implements OnInit {

    public dsDashboardScreen = new DashboardScreen();
    allRowsExpanded = false;
    expandedElement: any;
    isAdvSearched = false;
    isNotificationSelected = false;
    recordCount = 0;
    selectedBusinessId = (localStorage.businessGuid != undefined && localStorage.businessGuid != null && localStorage.businessGuid != "" ? parseInt(localStorage.businessGuid) : 0);
    filter: SideFilter;

    searchValue: any;
    classsValue: any;

    columnsToDisplay = ['TestName', 'Class', 'SampleRequirements', 'TubeType', 'TAT', 'Price', ' '];
    dataSource = new MatTableDataSource<any>();
    originData: any[] = [];
    dsAnalysisSource: any[] = [];

    @ViewChild(MatPaginator, { static: true }) paginator: MatPaginator;
    @ViewChild(MatSort, { static: true }) sort: MatSort;
    selectedRow: any;
    selectfalserow: any;
    active = true;
    proActive = false;
    public checked = false;
    filterClassData: any[] = [];
    filterMenuData: any[] = [];
    isChecked = false;
    public CheckForm: UntypedFormGroup = new UntypedFormGroup({});
    checkfavastatus: any;
    favstatus: any;
    defaultValue: any;
    selected: any;
    selectedMenu: any;
    filtereddata: any[];
    public userId: number = localStorage.authDetails ? JSON.parse(localStorage.authDetails).Id:undefined;
    dosprice: any;
    selectedColor: any;
    selectedmenufor: any;
    selectedmenudata: any;
    selectedClassdata: any;
    start = 0;
    limit = 15;
    end: number = this.limit + this.start;
    selectedRowIndex: number = null;

    public label: DosScreen;
    public isViewDOSPrice: boolean;

    constructor(
        public commonService: CommonService,
        public userService: UserService,
        private fb: UntypedFormBuilder,
        public sidemenuService: SidemenuService,
        public headerService: HeaderService,
        public patientService: PatientService,
        public labelService: LabelService,
        public dataService: DataService,
        private modalService: NgbModal,
        public sidefilterService: SidefilterService) {
        this.isViewDOSPrice = this.dataService.appData.authDetails['IsViewDOSPrice']; // // == 'True' ? true : false;
        this.sidemenuService.selectedModuleId = EnmModule.dos;
        this.labelService.getDosScreenLabels();
        this.label = this.labelService.dosScreen;

        this.setDefaultFilter();
        this.CheckForm = fb.group({
            checkfavastatus: [null]
        })
    }

    ngOnInit() {
        if (localStorage.getItem('alert-popup') == 'true') {
            this.modalService.open(AlertMessageComponent, {
                centered: true,
                backdrop: 'static',
                scrollable: false
            });
        }

        this.getDosfilter();
        this.getUsersettings();
        this.apply(this.originData, null);

    }
    onTableScroll(e) {
        const tableViewHeight = e.target.offsetHeight /** viewport */
        const tableScrollHeight = e.target.scrollHeight /** length of all table  */
        const scrollLocation = e.target.scrollTop; /**  how far user scrolled */

        /** If the user has scrolled within 200px of the bottom, add more data */
        const buffer = 200;
        const limit = tableScrollHeight - tableViewHeight - buffer;
        if (scrollLocation > limit) {
            const data = this.getTableData(this.start, this.end);
            this.dataSource.data = this.dataSource.data.concat(data);
            this.updateIndex();
        }
    }
    public getTableData(start, end) {
        return this.originData.filter((value, index) => index >= start && index < end);
    }
    public updateIndex() {
        this.start = this.end;
        this.end = this.limit + this.start;
    }
    public onDataSort() {
        const scrollabe = document.getElementById('dashboard-scrollabe');
        scrollabe.scrollTop = 0;
    }

    public bindDataSource() {

        this.dataSource = new MatTableDataSource<any>(this.originData);
        this.updateIndex();
        this.dataSource.sortingDataAccessor = this.commonService.sortingDataAccessor;
        this.dataSource.sort = this.sort;
        this.recordCount = this.dataSource.data.length;
        if (this.recordCount > 0 && this.isNotificationSelected) {
            this.allRowsExpanded = true;
            this.isNotificationSelected = false;
        }
        else {
            this.allRowsExpanded = false;
        }

    }

    public getDosfilter() {
        this.commonService.GetDOSFilterdetails().subscribe(data => {
            this.filterClassData = data.ResponseData.Class;
            this.filterMenuData = data.ResponseData.Menu;
        })
    }
    public setDefaultFilter() {
        this.selected = 'Filter By Class';
        this.selectedMenu = 'Filter By Menu';
        this.isAdvSearched = false;
    }
    /** required */
    public toggle() {
        this.allRowsExpanded = !this.allRowsExpanded;
        this.expandedElement = null;
    }
    // changeValue(value) {
    //     //    console.log(value)
    // }

    public setClickedtrueRow(element) {
        this.selectedRow = element;
        this.dataSource.data.forEach(data => {
            if (data.TestId == element.TestId) {
                data.IsFavourite = !data.IsFavourite;
            }
        })

        const fav = {
            TestId: element.TestId,
            UserGuid: this.headerService.loginUserGuid,
            IsFavourite: element.IsFavourite
        }
        this.commonService.postFavourite(fav).subscribe(() => {
            this.expandedElement = null;
        })
    }

    public searchDosTestData() {
        const dosfields = {
            SearchText: this.searchValue,
            IsOrder: false,
            UserGuid: this.headerService.loginUserGuid,
        }
        this.commonService.GetDOSTestdata(dosfields).subscribe(data => {
            this.originData = data.ResponseData;
            this.bindDataSource();
        }, err => {
            console.log(JSON.stringify(err));
            this.commonService.showCommonToastrError();
        });

    }

    public getDosTestData() {
        const dosfields = {
            SearchText: this.searchValue,
            ClassName: this.selectedClassdata,
            Menu: this.selectedmenudata,
            IsOrder: false,
            UserGuid: this.headerService.loginUserGuid,
            IsFavourite: this.CheckForm.controls['checkfavastatus'].value
        }
        this.commonService.GetDOSTestdata(dosfields).subscribe(data => {
            this.originData = data.ResponseData;
            this.bindDataSource();
        });

    }

    public apply(selectedmenu, selectedClass) {
        if (selectedmenu.length > 100) {
            selectedmenu = this.selectedmenufor;
        }
        if (selectedmenu == "Filter By Menu") {
            selectedmenu = "";
        }
        if (selectedClass == "Filter By Class") {
            selectedClass = "";
        }
        const selectedmenuA = selectedmenu.toString();
        this.selectedmenudata = selectedmenuA;
        this.selectedClassdata = selectedClass;

        this.getDosTestData();

    }
    public clraeAll() {
        if (this.searchValue != undefined || this.selectedmenudata != "" || this.selectedClassdata != null || this.CheckForm.controls['checkfavastatus'].value != null) {
            this.searchValue = undefined;
            this.selectedmenudata = "";
            this.selectedClassdata = null;
            this.CheckForm.controls['checkfavastatus'].setValue(null);
            this.CheckForm.reset();
            this.selectedMenu = 'Filter By Menu';
            this.selected = 'Filter By Class';
            this.selectedmenufor = 'Filter By Menu';
            this.getDosTestData();
        }
    }

    getUsersettings() {
        this.userService.getUserSettingDetails(this.headerService.loginUserGuid, localStorage.businessGuid).subscribe(data => {
            this.dosprice = data.ResponseData;
        });
    }

    public expandCard(ele) {
        this.expandedElement = this.expandedElement === ele ? null : ele
        gtag('event', 'Directory_of_Services', {
            Total_Services_Visited: '',
            Total_Unique_Services_Clicked: '',
            Service_Name: ele.TestName,
            Service_Class: ele.Class,
            Sample_Requirements: ele.SampleRequirements,
            Tube_Type: ele.TubeType,
            TAT: ele['TAT'],
            Price: ele.Price,
            Component: ele.Component,
            Notes: ele.Notes,
            Methodology: ele.Methodologies,
            Lab: ele.LabName,
        })
    }
}
