import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { RefLabGuard, IsReflabActive } from '../../guards';
import { RefrenceLabComponent } from './refrence-lab.component';
import { OrdersComponent } from './orders/orders.component';
import { CreateOrdersComponent } from './orders/create-orders/create-orders.component';
import { AddOrdersComponent } from './orders/add-orders/add-orders.component';

const routes: Routes = [
    {
        path: '',
        component: RefrenceLabComponent,
        canActivate: [RefLabGuard],
        children: [
            {
                path: "orders",
                component: OrdersComponent,
            },
            {
                path: "createOrder",
                component: CreateOrdersComponent,
                canActivate: [IsReflabActive]
            },
            {
                path: "addOrder",
                component: AddOrdersComponent,
                canActivate: [IsReflabActive]
            },
            // {
            //     path: "dos",
            //     component: DosComponent,
            //     canActivate: [RefLabGuard]
            // },
            {
                path: 'dos',
                loadChildren: () => import('src/app/modules/dos-page/dos-page.module').then((m) => m.DosPageModule)
            },
            //   {
            //     path: "accession-details",
            //     component: DetailsComponent,
            //     canActivate: [RefLabGuard]
            // }

        ]
    }
];
@NgModule({
    imports: [RouterModule.forChild(routes)],
    exports: [RouterModule]
})
export class RoutingModule { }
