  itemDisplayFn(item) {
    let str = "";
    if (item.OwnerName != undefined) {
      if (item.PatientId) str += item.PatientId + ", ";
      if (item.PatientFirstName) str += item.PatientFirstName;
      if (item.PatientLastName) str += " " + item.PatientLastName;
      if (item.OwnerName) str += ", " + item.OwnerName;
      if (item.Species) str += ", " + item.Species;
      if (item.Breed) str += ", " + item.Breed;
      if (item.Gender) str += ", " + item.Gender;
      if (item.Age) str += ", " + item.Age;
      if (item.LastResultDate) str += ", " + item.LastResultDate;
    }
    return str;
  }

