  public addOwnerDetails() {
    this.ownerDetailsForm.patchValue({
      OwnerLastName: this.ownerDetailsForm.get("OwnerLastName").value?.trim(),
      OwnerFirstName:
        this.ownerDetailsForm.controls["OwnerFirstName"].value?.trim(),
      PatientFirstName: this.ownerDetailsForm
        .get("PatientFirstName")
        .value?.trim(),
      PatientLastName:
        this.ownerDetailsForm.controls["PatientLastName"].value?.trim(),
      BreedId: 
        this.ownerDetailsForm.controls["BreedId"].value?.trim(),
      pmspatientId:
        this.ownerDetailsForm.controls["pmspatientId"].value?.trim()?this.ownerDetailsForm.controls["pmspatientId"].value?.trim():null
    });
    if (!this.ownerDetailsForm.valid) {
      for (const i in this.ownerDetailsForm.controls)
        this.ownerDetailsForm.controls[i].markAsTouched();
      const msg = "Please enter all required fields";
      this.confirmationDialogService
        .confirm("", msg, "OK", "Cancel", "sm", true, false)
        .then(() => {
          //something
        });
      return;
    } else {
      if (this.ownerDetailsForm.get("BusinessGuid").value == null) {
        this.ownerDetailsForm.patchValue({
          BusinessGuid: localStorage.businessGuid,
        });
      }
      
      const req = { ...this.ownerDetailsForm.value, ...{ UserGuid: this.dataService.appData.authDetails['UserGuid'] } }
      //console.log(this.ownerDetailsForm.value)
      this.refLabService
        .addPatientDetail(req)
        .subscribe(
          (data) => {
            if (data.StatusCode == 302) {
              this.ownerDetailsForm.patchValue({ pmspatientId: null });
              this.confirmationDialogService.confirm(
                "",
                "PIMS Patient ID already exist!",
                "OK",
                "Cancel",
                "sm",
                true,
                false
              );
              return;
            }
            this.new_patient_id = data.ResponseData;
            this.filterId = this.ownerDetailsForm.get("PatientId").value;
            this.getPatientOwnerDDLList(data.ResponseData);
            this.modalService.dismissAll();
            this.ownerDetailsForm.reset();
            this.ApiCalled = true;
          },
          (err) => {
            console.log(JSON.stringify(err));
          }
        );
    }
  }
