  public getBreedList(speciesId) {
    if (!speciesId) {
      this.breedList = [];
      return;
    }
    const langId = EnmLanguage.English;
    this.refLabService.getBreedListBySpeciesId(speciesId, langId).subscribe(
      (res) => {
        this.ownerDetailsForm.patchValue({ BreedId: "" });
        this.breedList = res.ResponseData;
        // this.breedList = [{ Id: "null", Name: "Select Breed", selected: true }].concat(this.breedList);
        console.log(this.breedList);
      },
      (err) => {
        console.log(JSON.stringify(err));
        this.commonService.showCommonToastrError();
      }
    );
  }

