  public onChange(Menu) {
    this.menuSelected = Menu;
    if (
      this.selectedfav != false &&
      this.classSelected != "" &&
      this.menuSelected != ""
    ) {
      this.filterDatafor = this.OrderTestData.filter(
        (x) =>
          x.IsFavourite &&
          x.Class == this.classSelected &&
          x.Menu == this.menuSelected
      );
    } else {
      if (this.classSelected != "" && this.menuSelected != "") {
        this.filterDatafor = this.OrderTestData.filter(
          (x) => x.Class == this.classSelected && x.Menu == this.menuSelected
        );
      } else {
        if (this.selectedfav != false && this.menuSelected != "") {
          this.filterDatafor = this.OrderTestData.filter(
            (x) => x.IsFavourite && x.Menu == this.menuSelected
          );
        } else {
          if (this.menuSelected != "") {
            this.filterDatafor = this.OrderTestData.filter(
              (x) => x.Menu.toLowerCase() == Menu.toLowerCase()
            );
          }
        }
      }
    }
  }
  public onChangeClass(cls) {
    this.classSelected = cls;
    if (
      this.selectedfav != false &&
      this.menuSelected != "" &&
      this.classSelected != ""
    ) {
      this.filterDatafor = this.OrderTestData.filter(
        (x) =>
          x.IsFavourite &&
          x.Menu == this.menuSelected &&
          x.Class == this.classSelected
      );
    } else {
      if (this.menuSelected != "" && this.classSelected != "") {
        this.filterDatafor = this.OrderTestData.filter(
          (x) => x.Menu == this.menuSelected && x.Class == cls
        );
      } else {
        if (this.selectedfav != false && this.classSelected != "") {
          this.filterDatafor = this.OrderTestData.filter(
            (x) => x.IsFavourite && x.Class == this.classSelected
          );
        } else {
          if (this.classSelected != "") {
            this.filterDatafor = this.OrderTestData.filter(
              (x) => x.Class.toLowerCase() == cls.toLowerCase()
            );
          }
        }
      }
    }
  }
  public onChangeFav(fav) {
    this.selectedfav = fav;
    if (
      this.menuSelected != "" &&
      this.classSelected != "" &&
      this.selectedfav != false
    ) {
      this.filterDatafor = this.OrderTestData.filter(
        (x) =>
          x.Menu == this.menuSelected &&
          x.Class == this.classSelected &&
          x.IsFavourite
      );
    } else {
      if (this.classSelected != "" && fav != false) {
        this.filterDatafor = this.OrderTestData.filter(
          (x) => x.Class == this.classSelected && x.IsFavourite
        );
      } else {
        if (this.selectedfav == true) {
          this.filterDatafor = this.OrderTestData.filter((x) => x.IsFavourite);
        }
      }
    }
  }
