import { Injectable } from '@angular/core';
import { Router } from '@angular/router';
import { UserService } from '../services/user.service';
import { ConfirmationDialogService } from '../components/confirmation-dialog/confirmation-dialog.service';
import { LabelService } from '../services/label.service';
import { EnmCountry } from '../shared/constants/enum';

@Injectable({
  providedIn: 'root'
})
export class AuthGuardRotor  {

  constructor(
    private router: Router,
    public userService: UserService,
    public confirmationDialogService: ConfirmationDialogService,
    public labelService: LabelService) {
  }

  canActivate() {
    //  40 for US && 31 for Canada
    if (this.labelService.userCountryId == EnmCountry.UnitedStates || this.labelService.userCountryId == EnmCountry.Canada) {
      return true;
    }
    return false;
  }

}





