import { Injectable } from '@angular/core';
import { ActivatedRouteSnapshot, RouterStateSnapshot, Router } from '@angular/router';
import { ConfirmationDialogService } from '../components/confirmation-dialog/confirmation-dialog.service';
import { DataService, LabelService } from '../services';

@Injectable({
    providedIn: 'root'
})
export class RefLabGuard  {

    public IsLinkedRefLabClinic = false;
    public isConsultant = false;
    public isLoggedIn = null;

    constructor(private router: Router, public dataService: DataService, public confirmationDialogService: ConfirmationDialogService, public labelService: LabelService) { }

    canActivate(route: ActivatedRouteSnapshot, state: RouterStateSnapshot, showPop = true) {
        this.isLoggedIn = JSON.parse(localStorage?.authDetails)?.access_token;
        if (this.dataService.appData.authDetails['IsLinkedRefLabClinic'] || this.dataService.appData.authDetails['IsConsultantUser']) {
            this.IsLinkedRefLabClinic = this.dataService.appData.authDetails['IsLinkedRefLabClinic'];
            this.isConsultant = this.dataService.appData.authDetails['IsConsultantUser'];
            // this.isLoggedIn = JSON.parse(localStorage?.authDetails)?.access_token;
        }
        
        if (this.isLoggedIn) {
            if (this.IsLinkedRefLabClinic || this.isConsultant) {
                return true;
            } else {
                // 'This service is not available for your clinic. Please contact your local representative for more information'
                const msg = this.labelService.dsSideMenuScreen.ConsultationRestrictionPopupMsg;
                if (showPop) {
                    this.confirmationDialogService.dismiss();
                    this.confirmationDialogService.confirm("Alert", msg, 'Ok', "Cancel", "sm", true, false);
                }
            }
        } else {
            this.router.navigateByUrl('/login')
        }
        return false;
    }

}
