import { Injectable } from '@angular/core';
import { Router } from '@angular/router';
// import { Observable } from 'rxjs';
// import { ConsultantGuard } from './consultant.guard';

@Injectable({
  providedIn: 'root'
})
export class AuthGuard  {

  private isLoggedIn;
  private BackOfficeFlag;
  private isConsultantUser;
  private navUrl: string;

  constructor(private router: Router) { }

  canActivate() {
    if (localStorage.authDetails != undefined) {
      this.isLoggedIn = JSON.parse(localStorage.authDetails).access_token;
      this.BackOfficeFlag = JSON.parse(localStorage.authDetails).BackOfficeFlag;
      this.isConsultantUser = JSON.parse(localStorage.authDetails).IsConsultantUser;
      this.navUrl = this.BackOfficeFlag == true ? '/internal' : '/login';
    }
    if (this.isLoggedIn) {
      return true;
    }
    this.router.navigateByUrl(this.navUrl)
    return false;
  }
  checkAuthentication() {
    if (this.isLoggedIn) {
      return true
    }
    return false
  }
}
