  selectedMenu: any;
  patientOwnerList: any;
  // selectedArray: any = [];
  filteredArray: any = [];
  selPatient: any;
  section = "testSelection";
  disabledBtn = true;
  filteredOptions: any = [];
  public menuSelected: "";
