import { Component, Input } from '@angular/core';
import { NgbActiveModal } from '@ng-bootstrap/ng-bootstrap';

@Component({
    selector: 'app-note-content',
    template: `
    <div class="modal-header">
      <h4 class="modal-title" style="font-weight: bold;"> 
      <!--  {{note.heading}} -->
      Accession Notes
      </h4>
    </div>
    <div class="modal-body">
    <!-- <p> <strong> {{note.title}}: </strong> </p> -->
    <p style="border: 1px solid gainsboro; padding: 14px;">{{note.text}}</p>
    </div>    
    <div style="margin-bottom: 20px; text-align: center;">
      <button mat-raised-button color="warn" style="min-width: 100px; background-color:#ff6400" (click)="activeModal.close('Close click')">{{note.btnText}}</button>
    </div>
    `
})
export class NoteContentComponent {
    @Input() note;
    constructor(public activeModal: NgbActiveModal) { }
}